




















import * as THREE from 'three'
import Vue from 'vue'
import { BackgroundManager } from '../scripts/Background1.0'

type Data = {
    fpm: BackgroundManager | null,
    loadingStat: {
        loaded: number,
        total: number,
        isLoaded: boolean,
        noLoadingStat: boolean
    }
}

export default Vue.extend({
    data(): Data {
        return {
            fpm: null,
            loadingStat: {
                loaded: 0,
                total: 1,
                isLoaded: false,
                noLoadingStat: false
            }
        }
    },
    mounted() {
        const self = this
        if (this.$refs['canvas-container'] instanceof HTMLElement) {
            this.fpm = new BackgroundManager(
                this.$refs['canvas-container'],
                () => {
                    console.log('loaded')
                    self.loadingStat.isLoaded = true
                    setTimeout(() => {
                        self.loadingStat.noLoadingStat = true
                    }, 501)
                },
                (_, loaded, total) => {
                    this.loadingStat.loaded = loaded
                    this.loadingStat.total = total
                },
                (url) => {
                    console.error('failed to load asset: ' + url)
                    self.loadingStat.isLoaded = true
                }
            )
        }
    }
})
